body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.header {
    background-color: #282c34;
    padding: 20px;
    color: white;
    text-align: center;
}

.footer {
    background-color: #282c34;
    padding: 10px;
    color: white;
    text-align: center;
}

/* New styles for 3D background + overlay UI */
.app-root {
    height: 100vh;
    width: 100vw;
    position: relative;
    overflow: hidden;
}

.canvas-wrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.overlay {
    position: relative;
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: white;
    pointer-events: none; /* let three.js capture canvas interactions if any */
}

.overlay-nav {
    display: flex;
    gap: 10px;
    padding: 20px;
    align-items: center;
    pointer-events: auto; /* nav needs to be clickable */
}

.nav-item {
    background: rgba(255,255,255,0.06);
    border: 1px solid rgba(255,255,255,0.08);
    color: white;
    padding: 8px 12px;
    border-radius: 6px;
    cursor: pointer;
    backdrop-filter: blur(4px);
}

.nav-item.active {
    background: linear-gradient(90deg, rgba(255,255,255,0.06), rgba(255,255,255,0.12));
    transform: translateY(-2px);
}

.content-wrapper {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 36px;
    padding: 40px;
    pointer-events: auto;
}

.page-section {
    max-width: 880px;
    background: rgba(0, 0, 0, 0.45);
    border-radius: 8px;
    padding: 22px;
    border: 1px solid rgba(255,255,255,0.04);
}

.page-section h1 { font-size: 30px; margin-bottom: 6px; }

/* make the canvas fill and keep everything readable on small screens */
@media (max-width: 760px) {
    .page-section { padding: 16px; }
    .overlay-nav { padding: 10px; }
}